.header-color{
    background: #BBD2C5;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #292E49, #536976, #BBD2C5);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #292E49, #536976, #BBD2C5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}

.landing-grid{
    background: #BBD2C5;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #292E49, #536976, #BBD2C5);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #292E49, #536976, #BBD2C5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */



  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: scroll;
}
.avatar-img{
  height: 250px;
  border-radius: 100px;
  padding: 50px;
}
.banner-text{
  background-color: white;
  opacity: .9;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  padding: 2px;
}
.resume-body{
  background-color: white;
  width: 80%;
  border-radius: 60px;
  margin: auto;
  height: auto;
  padding: 5em;
  opacity: .9;
}
.social-links{
  background-color: white;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: auto;
  border-radius: 10px;
  margin: auto;
  padding: 5em;
  opacity: .9;
}
.social-links i{
  color: #0077B5;
  font-size: 2em;
  padding: 10px;
}
.body{
  background-color: white;
  opacity: .9;
  width: 80%;
  margin: auto;
  border-radius: 10px;
  padding: 5px;
}
.about-paragraph{
  font-size: 20px;
  color: black;
  padding: 10px;
  line-height: 1.5em;
}
.profile-pic {
  padding: 50px;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  align-self: center;
}
.banner-text h1{
  font-size: 50px;
  font-weight: bold;
  color: black;
  line-height: 1.5em;
}
.banner-text hr{
  border-top: 10px linear black;
  width: 50%;
  margin: auto;
}
.banner-text p{
  color: black;
  font-size: 20px;
  padding:1em;
}
.projects-grid{
  display: flex;
}
